/* =Project Slides */
.projects {
  visibility: hidden;
  opacity: 0; }

.project {
  text-align: center;
  position: fixed;
  top: 50%;
  left: 0;
  bottom: 0;
  right: 0;
  transform: translateY(-50%); }

.project-image,
.project-image img,
.project-image:before,
.project-image:after {
  -webkit-border-radius: 100%;
  border-radius: 100%; }

.project-image:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(242, 236, 189, 0.8);
  top: -5px;
  left: -5px;
  z-index: 2; }

.project-image:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(121, 140, 29, 0.3);
  bottom: -6px;
  right: -6px;
  z-index: 3; }

.project-image {
  position: relative;
  width: 224px;
  height: 224px;
  margin: 0 auto;
  z-index: 5; }

.project-image img {
  position: relative;
  z-index: 4;
  display: block; }

.project-title {
  text-transform: uppercase;
  color: #F2ECBD;
  font-size: 30px;
  margin: 20px 0 0 0; }

.project-subtitle {
  font-size: 16px;
  color: rgba(242, 236, 189, 0.3);
  margin: 0;
  text-transform: lowercase;
  font-style: italic; }

.project-pixels {
  position: absolute;
  top: 70%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -100%); }

.pixel {
  width: 15px;
  height: 15px;
  background-color: rgba(242, 236, 29, 0.1);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-border-radius: 100%;
  border-radius: 100%;
  z-index: 3; }

.pixel01 {
  left: 100px;
  top: -130px; }

.pixel02 {
  left: 126px;
  top: -104px; }

.pixel03 {
  left: 120px;
  top: -90px; }

.pixel04 {
  left: 111px;
  top: -50px; }

.pixel05 {
  left: 120px;
  top: -10px; }

.pixel06 {
  left: 145px;
  top: -70px; }

.pixel07 {
  left: 160px;
  top: -90px; }

.pixel08 {
  left: 150px;
  top: -40px; }

.pixel09 {
  left: 179px;
  top: -45px; }

.pixel10 {
  left: 187px;
  top: -77px; }

.pixel11 {
  left: -230px;
  top: -60px; }

.pixel12 {
  left: -110px;
  top: -130px; }

.pixel13 {
  left: -126px;
  top: -104px; }

.pixel14 {
  left: -140px;
  top: -90px; }

.pixel15 {
  left: -151px;
  top: -50px; }

.pixel16 {
  left: -125px;
  top: -10px; }

.pixel17 {
  left: -165px;
  top: -70px; }

.pixel18 {
  left: -170px;
  top: -60px; }

.pixel19 {
  left: -190px;
  top: -40px; }

.pixel20 {
  left: -181px;
  top: -45px; }

.pixel21 {
  left: -193px;
  top: -77px; }

.pixel22 {
  left: -140px;
