.sizeS {
  width: 5px;
  height: 5px;
  background-color: rgba(242, 236, 189, 0.1); }

.sizeL {
  width: 15px;
  height: 15px;
  background-color: rgba(242, 236, 189, 0.3); }

.sizeXL {
  width: 30px;
  height: 30px;
  background-color: rgba(242, 236, 189, 0.6); }

.button-container {
  position: relative; }

a.button {
  text-decoration: none;
  width: 150px;
  display: block;
  margin: 0 auto;
  color: rgba(255, 255, 255, 0.7);
  padding: 8px 0;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%); }

.bp {
  width: 50px;
  height: 100%;
  border: 2px white solid;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0; }

.bp-mid {
  left: 50px;
  border-width: 1px 0; }

.bp-left {
  border-width: 1px 0 1px 1px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px; }

.bp-right {
  left: auto;
  right: 0;
  border-width: 1px 1px 1px 0;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px; }

